import './App.css';
import React from 'react';
import { Router } from 'react-router';
import LoginPage from './components/LoginPage';
import TestLoginForm from './components/TestLoginForm';
import firebase from './firebase';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import UserMainLayout from './components/UserMainLayout';
import RootMainLayout from './components/RootMainLayout';
import 'antd/dist/antd.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      authenticated: false,
      user: null,
    };
  }

  componentWillMount() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.setState({
          authenticated: true,
          currentUser: user,
          loading: false,
          user: user,
        });
      } else {
        this.setState({
          authenticated: false,
          currentUser: null,
          loading: false,
          user: null,
        });
      }
    });
  }
  logout = () => {
    firebase.auth().signOut();
  };
  render() {
    const { authenticated, loading } = this.state;
    if (loading) return <p>loading..</p>;
    return (
      <div className='App'>
        <BrowserRouter>
          <Route
            exact
            path='/*'
            render={() =>
              authenticated === true ? (
                <UserMainLayout
                  authenticated={this.state.authenticated}
                  user={this.state.user}
                  logout={this.logout}
                />
              ) : (
                // <Redirect to='/login' />
                // <LoginPage />
                <TestLoginForm />
              )
            }
          />
        </BrowserRouter>
      </div>
    );
  }
}

export default App;
