import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect,
} from 'react-router-dom';
import { Layout, Menu, Breadcrumb } from 'antd';
import {
  HomeOutlined,
  DesktopOutlined,
  FileOutlined,
  UserOutlined,
} from '@ant-design/icons';

const { Header, Content, Footer, Sider } = Layout;

class RootMainLayout extends React.Component {
  state = {
    authenticated: this.props.user,
    collapsed: false,
  };

  onCollapse = (collapsed) => {
    console.log(collapsed);
    this.setState({ collapsed });
  };

  render() {
    const { collapsed, authenticated } = this.state;
    console.log(authenticated);
    return authenticated === false ? (
      <Redirect to='/login' />
    ) : (
      <Router>
        <Layout style={{ minHeight: '100vh' }}>
          <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
            <div className='logo' />
            <Menu theme='dark' defaultSelectedKeys={['1']} mode='inline'>
              <Menu.Item key='1' icon={<HomeOutlined />}>
                <Link to='/'>HomePage</Link>
              </Menu.Item>
              <Menu.Item key='2' icon={<DesktopOutlined />}>
                <Link to='/searchpic'>Search</Link>
              </Menu.Item>
              <Menu.Item key='9' icon={<FileOutlined />}>
                <Link to='/stk'>Libralies</Link>
              </Menu.Item>
              <Menu.Item
                key='sub1'
                icon={<UserOutlined />}
                onClick={this.props.logout}
                title='User'
              >
                　 Log out
              </Menu.Item>
            </Menu>
          </Sider>
          <Layout className='site-layout'>
            <Header className='site-layout-background' style={{ padding: 0 }} />
            <Content style={{ margin: '0 16px' }}>
              <Breadcrumb style={{ margin: '16px 0' }}>
                <Breadcrumb.Item>User</Breadcrumb.Item>
                <Breadcrumb.Item>Bill</Breadcrumb.Item>
              </Breadcrumb>
              <div
                className='site-layout-background'
                style={{ padding: 24, minHeight: 360 }}
              >
                Bill is a cat.
              </div>
            </Content>
            <Footer style={{ textAlign: 'center' }}>made by Tuan</Footer>
          </Layout>
        </Layout>
      </Router>
    );
  }
}

export default RootMainLayout;
